<template>
  <div id="app" class="contain">
    <!--    搜索框-->
    <div>
      <el-container id="search">
        <el-header>
          <el-input v-model="search_text" placeholder="请输入基金相关内容"></el-input>
        </el-header>
      </el-container>
    </div>

    <!--    导航栏-->
    <div>
      <el-container class="menu">
        <el-tabs :activeName="menu_value" :text-color="text_color" @tab-click="handleClick" :stretch=true>
          <!--            <i class="el-icon-menu"></i>-->
          <el-tab-pane v-for="(item, index) in menu_names" :key='index' :label="item" :name="item">
          </el-tab-pane>
        </el-tabs>
      </el-container>
    </div>

    <!--    无限加载-->
    <div v-show="menu_value===menu_names[0]" class="recom">
      <ul class="infinite-list" v-infinite-scroll="load" style="overflow:auto">
        <li v-for="ele in recommandData" class="infinite-list-item">
          <el-container class="fund-load">
            <a :href="get_detail(ele.code)" target="view_window" color="inherit">
            <span v-html="recommand(ele)">
            </span>
              <span v-html="get_recom_index(ele.purchase_index)" id="reco_index"
                    :class="{ purchase: isPurchase(ele.purchase_index)}">
              </span>
              <span id="explain">购买指数</span>
            </a>
            <div>
              <canvas :id="ele.code" class="select-canvas"></canvas>
            </div>
          </el-container>
        </li>
      </ul>
    </div>
  </div>
</template>

<script type="text/babel">
  import 'element-ui/lib/theme-chalk/index.css'
  import 'element-ui/lib/theme-chalk/display.css'
  import F2 from '@antv/f2'

  export default {
    name: 'FundBase',
    data () {
      return {
        // 搜索
        search_text: '',

        // 导航
        menu_names: ['首页', '行情', '事件', '我的'],
        text_color: '#656980',
        menu_value: '首页',

        //推荐
        recommandData: [],
        page: 1
      }
    },
    mounted () {
      this.getData()
    },
    methods: {
      getData () {
        let params = {
          'page': this.page,
          'q': JSON.stringify({'filters': [{'name': 'period', 'op': '==', 'val': 90}]})
        }
        this.axios.get('http://106.12.117.104:5000/api/purchase_index',
          {
            params: params,
          }).then(res => {
          this.recommandData = this.recommandData.concat(res.data.objects)
          // console.log(res.data)
          this.plot()
        })
      },
      load () {
        this.page += 1
        if (this.page <= 10) {
          this.getData()
        }

      },
      handleClick (tab, event) {
        this.menu_value = tab.label
      },
      recommand (item) {
        return '&nbsp;&nbsp;' + item.name + '(' + item.code + ')'
      },
      get_detail (code) {
        return 'https://m.1234567.com.cn/?page=jjxq&code=' + code
      },
      get_recom_index (value) {
        return value.toFixed(2)
      },
      isPurchase (value) {
        return value >= 0.5
      },
      getHistoryData (code) {
        code = code.code

        function dateFormat (fmt, date) {
          let ret
          const opt = {
            'Y+': date.getFullYear().toString(),        // 年
            'm+': (date.getMonth() + 1).toString(),     // 月
            'd+': date.getDate().toString(),            // 日
            'H+': date.getHours().toString(),           // 时
            'M+': date.getMinutes().toString(),         // 分
            'S+': date.getSeconds().toString()          // 秒
            // 有其他格式化字符需求可以继续添加，必须转化成字符串
          }
          for (let k in opt) {
            ret = new RegExp('(' + k + ')').exec(fmt)
            if (ret) {
              fmt = fmt.replace(ret[1], (ret[1].length == 1) ? (opt[k]) : (opt[k].padStart(ret[1].length, '0')))
            }

          }

          return fmt
        }

        function formatTimeToStr () {
          var now = new Date()
          now.setDate(now.getDate() - 90)
          var d = dateFormat('YYYY-mm-dd', now)

          return d.toLocaleString()
        }

        let nowStr = formatTimeToStr()

        let paramsHistory = {
          'q': JSON.stringify({
            'filters': [{'name': 'code', 'op': '==', 'val': code},
              {'name': 'day', 'op': '>=', 'val': nowStr}]
          })
        }
        this.axios.get('http://106.12.117.104:5000/api/fund_history',
          {
            params: paramsHistory,
          }).then(res => {
          // console.log(res);
          const data = res.data.objects
          const chart = new F2.Chart({
            id: code,
            pixelRatio: window.devicePixelRatio
          })

          chart.source(data, {
            // sum_value: {
            //   tickCount: 2,
            //   min: 0
            // },
            day: {
              type: 'timeCat',
              range: [0, 1],
              tickCount: 3
            }
          })
          chart.tooltip({
            custom: true,
            showXTip: true,
            showYTip: true,
            snap: true,
            crosshairsType: 'xy',
            crosshairsStyle: {
              lineDash: [2]
            }
          })
          chart.axis('day', {
            label: function label (text, index, total) {
              const textCfg = {}
              if (index === 0) {
                textCfg.textAlign = 'left'
              } else if (index === total - 1) {
                textCfg.textAlign = 'right'
              }
              return textCfg
            }
          })
          chart.line().position('day*sum_value')
          chart.render()
        })
      },
      plot () {
        this.$nextTick(function () {
          this.recommandData.forEach(this.getHistoryData)
        })
      }
    },

  }
</script>
<style>
  #app {
    /*display: flex;*/
    margin: 10px auto;
    width: 100%;
    background: #fff;
    font-family: Arial;
  }

  /*搜索*/
  el-input {
    text-align: center;
  }

  input {
    text-align: center;
  }

  #search {
    flex-direction: column;
  }

  /*导航*/
  .menu {
    display: flex;
    justify-content: center;
    width: 100%;
  }

  .el-tabs--top {
    width: 100%;
    height: 50px;
  }


  /*无限列表*/
  .infinite-list {
    padding-top: 10px;
  }

  ul, li {
    list-style: none;
    text-align: left;
    margin: 0;
    padding: 0;
  }

  li {
    display: inline
  }

  .fund-load {
    width: 100%;
    height: 200px;
    /*background: #EEEEEE;*/
    display: flex;
    flex-direction: column;
    /*border: grey 0.05px;*/
    /*border-style: solid;*/
  }

  a {
    text-decoration: none;
    color: #333;
  }

  #reco_index {
    /*display:inline-flex;*/
    float: right;
    padding-right: 10px;
    color: green;
  }

  .purchase {
    color: red !important;
  }

  #explain {
    font-size: 50%;
    float: right;
    padding-right: 1px;
  }

  .select-canvas {
    display: block;
    margin: 0 auto;
    font-size: 20%;
  }
</style>



